import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'about',
      component: () => import('../views/About/index.vue')
    },
    {
      path: '/map',
      component: () => import('../views/Main'),
      children: [
        {
          path:'/',
          component: () => import('../views/Main/ecology.vue'),
          name:'ecology',
        },
        {
          path:'/map/ecology',
          component: () => import('../views/Main/ecology.vue'),
          name:'ecology',
        },
        {
          path:'/map/radiation',
          component: () => import('../views/Main/radiation.vue'),
          name:'radiation',
        },
      ]
    },
    {
      path: '*',
      name: '404',
      component: () => import('../views/404')
    }
  ],
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})
