const state = {
  prunedTreesArray: [],
  newTreesArray: [],
  removedTreesArray: [],
  activeMarkerEcology: {},
  filterEcology: ''
}

const mutations = {
  GET_PRUNED_TREES: (state, payload) => {
    return state.prunedTreesArray = payload
  },
  PUSH_PRONUDED_TREES_ARRAY (state, payload) {
    state.prunedTreesArray.push(payload)
  },
  GET_NEW_TREES: (state, payload) => {
    return state.newTreesArray = payload
  },
  PUSH_NEW_TREES_ARRAY (state, payload) {
    state.newTreesArray.push(payload)
  },
  GET_REMOVED_TREES: (state, payload) => {
    return state.removedTreesArray = payload
  },
  PUSH_REMOVED_TREES_ARRAY (state, payload) {
    state.removedTreesArray.push(payload)
  },
    GET_ACTIVE_MARKER_ECOLOGY (state, payload) {
      return state.activeMarkerEcology = payload
    },
    GET_FILTER_ECOLOGY (state, payload) {
      return state.filterEcology = payload
    }
}

const actions = {
  pushArrayPronudedTrees ({commit}, payload) {
    commit('PUSH_PRONUDED_TREES_ARRAY', payload)
  },
  pushArrayNewTrees ({commit}, payload) {
    commit('PUSH_NEW_TREES_ARRAY', payload)
  },
  pushArrayRemovedTrees ({commit}, payload) {
    commit('PUSH_REMOVED_TREES_ARRAY', payload)
  },
  addActiveMarkerEcology({commit}, payload) {
    commit('GET_ACTIVE_MARKER_ECOLOGY', payload)
  },
  filterEcology({commit}, payload) {
    commit('GET_FILTER_ECOLOGY', payload)
  },
}

const getters = {
  getPrunedTreesArray: state => state.prunedTreesArray,
  getNewTreesArray: state => state.newTreesArray,
  getRemovedTreesArray: state => state.removedTreesArray,
  getActiveMarkerEcology: state => state.activeMarkerEcology
}

export default {
  state,
  mutations,
  actions,
  getters
}
