const state = {
  stations: [
      {
        name: 'ЗАЕС',
        coords: {
          lat: '47.499',
          lan: '34.629'
        },
        mainInfo: []
      },
    {
      name: 'РАЕС',
      coords: {
        lat: '51.343',
        lan: '25.849'
      },
      mainInfo: []
    },
    {
      name: 'ХАЕС',
      coords: {
        lat: '50.305',
        lan: '26.645'
      },
      mainInfo: []
    },
    {
      name: 'ЮУАЕС',
      coords: {
        lat: '47.812',
        lan: '31.217'
      },
      mainInfo: []
    }
  ],
  activeMarkerRadiation: {}
}

const mutations = {
  PUSH_MAIN_INFO_ZAES (state, payload) {
    state.stations[0].mainInfo.push(payload)
  },
  PUSH_MAIN_INFO_RAES (state, payload) {
    state.stations[1].mainInfo.push(payload)
  },
  PUSH_MAIN_INFO_HAES (state, payload) {
    state.stations[2].mainInfo.push(payload)
  },
  PUSH_MAIN_INFO_YUAES (state, payload) {
    state.stations[3].mainInfo.push(payload)
  },
  GET_ACTIVE_MARKER_RADIATION (state, payload) {
    return state.activeMarkerRadiation = payload
  }
}

const actions = {
  pushMainInfoZaes ({commit}, payload) {
    commit('PUSH_MAIN_INFO_ZAES', payload)
  },
  pushMainInfoRaes ({commit}, payload) {
    commit('PUSH_MAIN_INFO_RAES', payload)
  },
  pushMainInfoHaes ({commit}, payload) {
    commit('PUSH_MAIN_INFO_HAES', payload)
  },
  pushMainInfoYuaes ({commit}, payload) {
    commit('PUSH_MAIN_INFO_YUAES', payload)
  },
  addActiveMarkerRadiation({commit}, payload) {
    commit('GET_ACTIVE_MARKER_RADIATION', payload)
  },
}

const getters = {
  getZaes: state => state.stations[0],
  getRaes: state => state.stations[1],
  getHaes: state => state.stations[2],
  getYuaes: state => state.stations[3],
  getActiveMarkerRadiation: state => state.activeMarkerRadiation
}

export default {
  state,
  mutations,
  actions,
  getters
}
